import { NextApiRequest, NextApiResponse } from "next";
import dbConnect from "../../lib/mongodb";
import Transaction from "../../models/TransactionModel";
import Income from "../../models/IncomeModel";
import Expense from "../../models/ExpenseModel";
import User from "../../models/UserModel";
import { Types } from "mongoose";

export default async function handler(
  req: NextApiRequest,
  res: NextApiResponse
) {
  // Connect to the MongoDB database
  await dbConnect();

  switch (req.method) {
    case "POST":
      const { userId, type, amount, description, date } = req.body;

      // Check if all required fields are provided
      if (!userId || !type || !amount || !description || !date) {
        return res.status(400).json({ message: "Semua field harus diisi" });
      }

      // Validate if userId is a valid ObjectId
      if (!Types.ObjectId.isValid(userId)) {
        return res.status(400).json({ message: "Invalid User ID" });
      }

      // Validate amount to ensure it's a valid number
      if (isNaN(amount) || amount <= 0) {
        return res
          .status(400)
          .json({ message: "Amount must be a positive number" });
      }

      // Validate date to ensure it's a valid date string
      const parsedDate = new Date(date);
      if (isNaN(parsedDate.getTime())) {
        return res.status(400).json({ message: "Invalid date format" });
      }

      try {
        let newTransaction;

        // Create a transaction based on the type
        if (type === "income") {
          newTransaction = new Income({
            userId: new Types.ObjectId(userId),
            amount,
            description,
            date: parsedDate,
          });
        } else if (type === "expense") {
          newTransaction = new Expense({
            userId: new Types.ObjectId(userId),
            amount,
            description,
            date: parsedDate,
          });
        } else {
          return res.status(400).json({ message: "Invalid transaction type" });
        }

        // Save the new transaction to the respective collection
        await newTransaction.save();

        // Find the user and update their transactions array
        const user = await User.findById(userId);
        if (!user) {
          return res.status(404).json({ message: "User not found" });
        }

        // Add the transaction's ID to the user's transactions array
        user.transactions.push(newTransaction._id);
        await user.save();

        // Return the newly created transaction
        res.status(201).json(newTransaction);
      } catch (error) {
        console.error("Error creating transaction:", error);
        res.status(500).json({
          message: "Terjadi kesalahan saat menambahkan transaksi",
        });
      }
      break;

    case "GET":
      const {
        userId: queryUserId,
        type: transactionType,
        lastSummary,
      } = req.query;

      // Validate userId query parameter
      if (!queryUserId || !Types.ObjectId.isValid(queryUserId as string)) {
        return res.status(400).json({ message: "Invalid or missing userId" });
      }

      try {
        // If `lastSummary` is true, fetch the latest 5 transactions
        if (lastSummary === "true") {
          const lastTransactions = await Transaction.find({
            userId: queryUserId,
          })
            .sort({ date: -1 })
            .limit(5); // Limit to 5 most recent transactions
          return res.status(200).json(lastTransactions);
        }

        // Create filter for userId
        const filter: any = {
          userId: new Types.ObjectId(queryUserId as string),
        };

        // If transactionType is specified, filter transactions by type (income or expense)
        if (
          transactionType &&
          (transactionType === "income" || transactionType === "expense")
        ) {
          // Fetch data from the respective collection
          if (transactionType === "income") {
            const incomeTransactions = await Income.find(filter);
            return res.status(200).json(incomeTransactions);
          } else if (transactionType === "expense") {
            const expenseTransactions = await Expense.find(filter);
            return res.status(200).json(expenseTransactions);
          }
        }

        // Fetch all transactions if no type is specified
        const transactions = await Transaction.find(filter);

        if (transactions.length === 0) {
          return res.status(404).json({ message: "No transactions found" });
        }

        // Return the list of transactions
        res.status(200).json(transactions);
      } catch (error) {
        console.error("Error fetching transactions:", error);
        res.status(500).json({
          message: "Terjadi kesalahan saat mengambil transaksi",
        });
      }
      break;

    default:
      res.setHeader("Allow", ["POST", "GET"]);
      res.status(405).json({ message: `Method ${req.method} Not Allowed` });
      break;
  }
}
